body{background-color:#29353f;color:#fff;padding:0;box-sizing:border-box;margin:0;font-family:"Open Sans";-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}a{text-decoration:none;color:#fff;font-family:source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace}p>a{-webkit-text-decoration:underline solid 2px rgba(255,255,255,0.7);text-decoration:underline solid 2px rgba(255,255,255,0.7)}.page-container{display:flex;flex-direction:column;align-items:center;justify-content:flex-start;font-size:1.25rem;min-height:80vh}.page-container>div{width:calc(min(80vw, 45rem))}.info-box{border-radius:2rem;background-color:#1e242d;padding:1.5rem;transition:ease 0.5s}.info-box:hover{transform:scale(1.03);transform-origin:50%, 50%}.info-box:hover img:hover{transform:scale(1.025);transform-origin:50%, 50%}.center{text-align:center}h1,h2,h3{font-family:Montserrat;margin-left:0rem;margin-top:0.5rem;margin-bottom:0.5rem}hr{width:auto;height:0;border:0.2rem solid #1e242d;border-radius:15px}ul{padding-left:1.5rem}ul>li{padding-bottom:0.25rem}img{display:block;margin-left:auto;margin-right:auto;height:250px;max-width:calc(min(100%, 25rem));border-radius:2rem;transition:ease-out 0.4s}.languages{font-family:source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;font-style:italic}

div.navbar{width:100%;height:calc(min(15vw, 4rem));background-color:#1e242d}nav.nav-elements{display:flex;height:100%;flex-direction:row;justify-content:flex-end;align-items:center}.navLink{font-size:calc(min(10vw, 3rem) / 2);height:100%;transition:ease 0.05s}.navLink:hover{background-color:#29353f}.navLink:hover>.nav-element{transform:translate(0, -0.2rem)}.nav-element{display:flex;justify-content:center;align-items:center;height:100%;margin-left:5vw;margin-right:5vw;transition:ease-out 0.4s}

#footer{right:0;bottom:0;left:0;height:auto;text-align:center}#footer>nav{padding:2rem}a.social-button{display:inline-block;min-width:5rem;margin:1rem;padding:0.75rem;border-radius:20px;border:1px solid #1e242d;background-color:#1e242d;transition:0.2s;font-size:1.25rem}a.social-button:hover{background-color:#29353f;border:3px solid #1e242d}

